#logo {
  font-size: 1.5em;
  font-family: "Hanken Grotesk Black";
}

#athena-nav p, a {
  font-family: "Hanken Grotesk Black";
  color: black;
  font-size: 1em;
}

#athena-nav {
  padding-top: 10vh;
  padding-left: 15vw;
  padding-right: 15vw;
  background-color: #F3EAFD;
}

@font-face {
  font-family: "Hanken Grotesk";
  src: url(/static/media/HankenGrotesk-Regular.8aa432c0.eot);
  src:url(/static/media/HankenGrotesk-Regular.a3d1b1bb.woff)format("woff"),
  url(/static/media/HankenGrotesk-Regular.9e8bb12f.otf)format("opentype")
}

@font-face {
  font-family: "Hanken Grotesk Black";
  src: url(/static/media/HankenGrotesk-Black.499224d6.eot);
  src:url(/static/media/HankenGrotesk-Black.ea8cbdeb.woff)format("woff"),
  url(/static/media/HankenGrotesk-Black.616709b8.otf)format("opentype")
}

@font-face {
  font-family: "Hanken Grotesk Medium";
  src: url(/static/media/HankenGrotesk-Medium.11157450.eot);
  src:url(/static/media/HankenGrotesk-Medium.7ed163a6.woff)format("woff"),
  url(/static/media/HankenGrotesk-Medium.15c02b41.otf)format("opentype")
}

@font-face {
  font-family: "Work Sans";
  src: url(/static/media/WorkSans-Regular.30be604d.ttf);
}

#about-row {
  height: 80vh;
  background-color: #F3EAFD;
  width: 100%;
  padding-left: 16vw;
  padding-right: 16vw;
}

#about-h {
  margin-top: 20vh;
  margin-right: -2vw;
}

#athena-hero {
  margin-top: 10vh;
}

#underlined-h:after {
  content: url(/static/media/underline-header.2278658b.svg);
  position: absolute;
  margin-top: 10px;
  margin-left: -135px;
}

@media screen and (max-width: 600px) {
  #about-row {
    height: 15em;
  }

  #about-text {
    padding-top: 5%;
  }

  #about-button {
    font-size: 2.5vw;
  }

  #small-descriptor {
    height: 15vh;
  }

  #small-descriptor p, #second-small-descriptor  p {
    font-size: 2vw;
    color: white;
  }
}

#pathways-row {
  width: 100%;
  padding-top: 2vh;
  padding-bottom: 5vh;
}

#pathways-header {
  text-align: center;
  padding-top: 5vh;
}

#pathways-row img {
  padding: 25px;
  height: 240px;
}

#pathways-row .athena-primary {
  font-size: 0.8em;
}

#path-cards p {
  font-size: 0.9em;
  text-align: left;
}

#path-cards {
  padding-top: 5vh;
  padding-left: 16vw;
}

#path-card {
  height: 420px;
  width: 250px;
  border: 1px solid #F3EAFD;
  margin-right: 72px;
  display: -webkit-inline-flex;
  display: inline-flex;
  padding-bottom: 5px;
}

#underlined:after{
  content: url(/static/media/underline-association.924864f3.svg);
  position: absolute;
  margin-top: 14px;
  margin-left: -120px;
}
#underlined-companies:after{
  content: url(/static/media/underline-companies.70e4b48e.svg);
  position: absolute;
  margin-top: 14px;
  margin-left: -105px;
}
#underlined-schools:after{
  content: url(/static/media/underline-schools.fba1cd78.svg);
  position: absolute;
  margin-top: 10px;
  margin-left: -75px;
}

#reason-header {
  text-align: center;
  padding-top: 5vh;
  padding-bottom: 5vh;
}

#reason-row {
  background-color: #F3EAFD;
  width: 100%;
  padding-top: 2vh;
  padding-left: 16vw;
  padding-right: 16vw;
  padding-bottom: 5vh;
  overflow: auto;
}

#top-row, #bottom-row {
  display: -webkit-inline-flex;
  display: inline-flex;
  padding-bottom: 2vh;
}

#bottom-row {
  padding-left: 5vw;
  padding-bottom: 5vh;
}

#stats-img {
  margin-right: 6vw;
}

#stats-img p {
  font-weight: bold;
}

#stats-img p:after {
  content: url(/static/media/underline-schools.fba1cd78.svg);
  position: absolute;
  margin-top: 14px;
  margin-left: -90px;
}

#reason-footer {
  text-align: center;
}

#footer-row {
  padding-top: 5vh;
  padding-left: 11vw;
  padding-right: 11vw;
}

#footer-row h4 {
  font-size: 0.8em;
}
#footer-row p, ol {
  font-size: 0.6em;
  list-style-type: none;
}

#company-row, #school-row, #association-row {
  padding-top: 5vh;
  padding-left: 16vw;
  padding-right: 16vw;
}

#company-header, #school-header, #association-header {
  padding-top: 15vh;
  padding-left: 52vw;
  height: 30vh;
  background-color: #F3EAFD;
}

#association-header h1, #school-header h1, #company-header h1 {
  margin-top: 5vh;
}

#association-header h1:before {
  content: url(/static/media/association-header.0c34f956.svg);
  position: absolute;
  margin-top: -15vh;
  margin-left: -17vw;
}

#school-header h1:before {
  content: url(/static/media/school-header.ba88906e.svg);
  position: absolute;
  margin-top: -17vh;
  margin-left: -14vw;
}

#company-header h1:before {
  content: url(/static/media/company-header.55bd217b.svg);
  position: absolute;
  margin-top: -17vh;
  margin-left: -12vw;
}


.athena-small-widget {
  height: 45vh;
  background: url(/static/media/rit.99ccfd66.png);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  border-color: #F2F1F4;
}

.modal-table {
  margin-top: 5%;
  margin-left: 10%;
  margin-bottom: 5%;
}

.modal-table td {
  padding-left: 2%;
  width: 25%;
  border-left: 1px solid black;
}

.athena-modal-header h3 {
  padding-left: 12%;
}

.athena-modal-body h4 {
  padding-left: 12%;
}

.athena-modal-body h5 {
  padding-left: 12%;
  font-size: 1em;
  color: #505051;
}

.athena-modal-body p {
  color: #505051;
}

.athena-modal-footer {
  padding-right: 45%;
}

#card-body {
  height: 10vh;
  color: white;
  background: rgba(154, 141, 164, 0.9);
}

.modal-table p {
  font-size: 1em;
}

.athena-small-widget p {
  font-size: 0.9em;
}

.athena-small-widget h3 {
  text-align: center;
  padding-top: 25%;
  font-size: 1.5em;
}

.ranking-badge {
  font-size: 1.2em;
  padding-left: 5%;
  padding-right: 5%;
  background-color: inherit;
  border-bottom: solid 2.5px white;
}

.modal-table .no-border {
  border: none;
}

.review {
  font-style: italic;
}

.add-new-button {
  margin-top: 5%;
}

@media screen and (max-width: 600px) {
  .athena-small-widget h3 {
    font-size: 1em;
  }

  .athena-small-widget {
    height: 20vh;
  }

  .athena-small-widget h3 {
    padding-top: 10%;
    padding-bottom: 15%;
  }

}

.sign-up, .sign-in {
  margin-top: 5vh;
  margin-left: 30vw;
  width: 35vw;
  border: solid 1px #F3EAFD;
  padding-top: 2vh;
  padding-left: 4vw;
  padding-right: 4vw;
}

Form {
  font-family: "Work Sans";
  padding-top: 1vh;
  padding-bottom: 5vh;
}

Form p {
  font-family: "Work Sans";
  font-size: 0.8em;
  color: red;
}

.sign-up h5, .sign-in h5 {
  font-family: "Work Sans";
  font-size: 0.8em;
}

.page-title {
  margin-top: 2%;
  margin-bottom: 2%;
  text-align: center;
}

.athena-form {
  border: 0.5px solid grey;
  padding: 5%;
  margin-top: 2%;
}

.athena-radio {
  display: -webkit-inline-flex;
  display: inline-flex;
  padding-left: 8%;
}

.rating-table {
  padding-top: 5%
}

.athena-question {
  font-weight: bolder;
}

html, body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Work Sans",
    sans-serif;
  font-size: 1rem;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

div, Button, .ranking-badge, Input {
  border-radius: 0 !important;
}

h1 {
  font-family: "Work Sans";
  font-weight: bold;
  font-size: 2.2em;
}

h2 {
  font-family: "Work Sans";
  font-size: 2.5em;
}

h3 {
  font-family: "Work Sans";
  font-size: 1.2em;
}

h5, p {
  font-family: "Work Sans";
  font-size: 1em;
}

.athena-primary {
  font-family: "Work Sans";
  font-weight: bold;
  background-color: transparent;
  padding: 0;
  border: none;
  color: black;
  text-align:left;
  font-size: 1em;
}

.athena-primary:hover {
  text-decoration: underline;
  background-color: #F3CD1A;
  color: black;
}

